 can turn off every appliances at home and let the car know he is coming. The car will adjust the in-car climate, seat positions and other settings to his preferences.\n        ")]),_c('v-img',{attrs:{"src":require("@/assets/audi/story-1.gif"),"contain":""}}),_c('p',{staticClass:"section"},[_vm._v("\n          Since he was watching TV when he left home, the system continues playing the news he was listening to when he enters the car.\n          "),_c('br'),_c('br'),_vm._v("Before he starts to drive, the system also let him know there's a road construction on his everyday route, thanks to car to car connectivity, Jack can take a look at the live situation and make a decision. With advanced gesture detection technology, Jack controls the system uses his fingers as buttons.\n        ")]),_c('v-img',{attrs:{"src":require("@/assets/audi/story-2.gif"),"contain":""}}),_c('p',{staticClass:"section"},[_vm._v("During driving, Jack wants to buy a coffee. Because the car is connected with all the stores, it filters out irrelevant information and displays which store is close by and how long he needs to wait. Jack simply uses his hand as a slider to make the selection. After he chooses the store, the system guides him to available parking location. With augmented reality technology, the windshield becomes a giant heads-up display and maps information onto the reality.")]),_c('v-img',{attrs:{"src":require("@/assets/audi/story-3.gif"),"contain":""}}),_c('p',{staticClass:"section"},[_vm._v("At the traffic light, Jack can see the count down on the red light. The system also warns him when an ambulance is coming because everything is connected. Jack can see through the car in front of him to be more aware of the surroundings. The system also shows areas he can't see in other situations to help him drive safely. E.g. pedestrians crossing the road, bikers in his blind spots, foggy weather or even a dark night.")]),_c('v-img',{attrs:{"src":require("@/assets/audi/story-4.gif"),"contain":